var _self;
export default {
	data() {
		return {
			graceSkeleton : 'ing',
			article : {
				contents : []
			}
		}
	},
	onLoad() {
		_self = this;
		// 加载文章详情
		uni.showLoading({});
		uni.request({
			url: 'https://www.easy-mock.com/mock/5cb9655c01e2e57715d324b0/artinfo_skeleton',
			method: 'GET',
			data: {},
			success: res => {
				// 此处先规划骨架
				var art = {contents : []};
				for(var i = 0; i < res.data.contents.length; i++){
					art.contents.push({'type': res.data.contents[i].type});
				}
				_self.article =  art;
				
				// 骨架屏规划后延长 500 毫秒进行数据替换
				setTimeout(function(){
					_self.article = res.data;
					_self.graceSkeleton = 'end';
					uni.setNavigationBarTitle({title : _self.article.title});
				}, 500);
			},
			fail: () => {},
			complete: () => {
				uni.hideLoading();
			}
		});
	},
	methods: {

	}
}
